aporlaofertaApp
    .directive('ngFooter', function () {
        return {
            restrict: 'A',
            templateUrl: 'resources/js/footer/footer.jsp',
            controller: ['$scope', '$rootScope',
                function ($scope, $rootScope) {
                    $scope.displayTutorialModal = function () {
                        $rootScope.$broadcast('displayTutorial');
                    }
                }]
        }
    });


